.input {
  position: relative;
  margin-bottom: 28px; }

.input__field {
  width: 100%;
  padding: 13px 40px 14px;
  font: inherit;
  font-weight: 700;
  text-transform: uppercase;
  color: rgba(72, 72, 72, 0.87);
  border: 2px solid #e3e3e3;
  border-radius: 80px;
  background-clip: padding-box;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.input__field::-webkit-input-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input__field::-moz-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input__field:-ms-input-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input__field::-ms-input-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input__field::placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input__field:valid {
  border-color: rgba(72, 72, 72, 0.5); }

.input__field:not(:placeholder-shown):invalid {
  border-color: rgba(255, 0, 0, 0.5); }

.input__field:focus {
  outline: none;
  border-color: transparent; }

.input__field:focus ~ .input__label::before {
  background-image: linear-gradient(108deg, #ffc341 20%, #ffd701 65%); }

.input__label {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%; }

.input__label::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 80px; }

.input__error {
  position: absolute;
  top: 100%;
  left: 24px;
  font-size: 13px;
  line-height: 1.5;
  color: #ff0000; }

